const util = require('../utils/util.js');

const api = require('../config/api.js');

/**
 * 
 */

// 
function isWx() {
	let uAgent = navigator?.userAgent?.toLowerCase && navigator.userAgent.toLowerCase();
	return /micromessenger/.test(uAgent) ? true : false;
}

function payOrder(orderId) {
	return new Promise(function(resolve, reject) {

		util.request(api.PayPrepayId, {
			orderId: orderId,
			isWx: isWx(),
		}).then((res) => {
			console.log(res);

			if (res.errno === 0) {
				const payParam = res.data;

				// jWeixin.config({
				// 	debug: false, // ,apialertpclogpc
				// 	appId: 'wxf84451020f439de9', // 
				// 	timestamp: payParam.timeStamp, // 
				// 	nonceStr: payParam.nonceStr, // 
				// 	signature: payParam.signType, // 1
				// 	jsApiList: ['chooseWXPay'] // JSJS2
				// })








































				uni.requestPayment({
					timeStamp: payParam.timeStamp,
					nonceStr: payParam.nonceStr,
					package: payParam.package,
					signType: payParam.signType,
					paySign: payParam.paySign,
					success: function(res) {
						resolve(res);
					},
					fail: function(res) {
						reject(res);
					},
					complete: function(res) {
						reject(res);
					}
				});

			} else {
				reject(res);
			}
		});
	});
}

module.exports = {
	payOrder
};
